import React from "react";
import logo from "./../assets/imagenes/logo.png"
import "./../assets/css/pie.css";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope} from '@fortawesome/free-solid-svg-icons'
import { faFacebook, faInstagram } from '@fortawesome/free-brands-svg-icons'

import { Link } from "react-router-dom";

class Pie extends React.Component {
    constructor() {
        super()
        this.state = {
            'titulo': "El mejor cuidado para tu coche"
        };
    }
    render() {
        return (
            <footer>
                <h3> {this.state.titulo} </h3>
                <div className="contenedor_footer">
                    <div className="contenedor_pie">
                        <h4>Quiénes somos</h4>
                        <Link className="enlace_pie" to={"/"}>Inicio</Link>
                        {/* <Link className="enlace_pie" to={"/servicios"}>Servicios</Link> */}
                        <Link className="enlace_pie" to={"/contacto"}>Contacto</Link>
                    </div>
                    <div className="contenedor_pie">
                        <h4>Informacion de contacto</h4>
                        <p>Email: {this.props.correo}</p>
                        <p>Teléfono: {this.props.telefono} </p>
                    </div>
                    <div className="contenedor_pie">
                        <h4> Nuestras redes sociales</h4>
                        {/* Facebook */}
                        <a target="blank" href="https://www.facebook.com/garajeguzmanleon"  aria-label="Siguemos en Facebook"> 
                            <FontAwesomeIcon className="iconos-rs" aria-hidden="true" icon={faFacebook} />
                        </a>
                        {/* <!-- Instagram--> */}
                        <a target="blank" href="https://www.instagram.com/garaje_guzman/?hl=es"  aria-label="Nuestro Instagram"> 
                            <FontAwesomeIcon className="iconos-rs" aria-hidden="true" icon={faInstagram} />
                        </a>

                        {/* <!-- Correo electronico --> */}
                        <a href="mailto:" aria-label="Nuestro correo">
                            <FontAwesomeIcon className="iconos-rs" aria-hidden="true" icon={faEnvelope} />
                        </a>
                    </div>
                </div>
                <div className="imagen-pie">
                    <img loading="lazy" id="logo_pie" src={logo} alt="Icono de la empresa " width="110" height="110"/>
                </div>
                <div className="copy">
                    <p>Diseño:&nbsp; 
                        <a href="https://www.linkedin.com/in/daniel-fernandez-alonso-90a949193/" target="_blank" rel="noopener noreferrer">
                            Daniel Fernández Alonso
                        </a> | 
                        Copyright © 2021 Las Chozas de Francisca
                    </p>
                </div>
            </footer>
        );
    }
}

export default Pie;