import './App.css';
import Menu from "./components/Menu"
import Pie from "./components/Pie"
import Principal from "./views/Index"
import Contacto from "./views/Contacto"

import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { createBrowserHistory } from "history";
const customHistory = createBrowserHistory();

function App() {
  var telefono = "987 23 73 22";
  var correo = "garajeguzman@gmail.com";
  return (
    <div className="App">
      <Router history={customHistory}>
        {/* Menu de la aplicacion */}
        <Menu></Menu>

        {/* Creador de las rutas dinamicas */}
        <Switch>
          {/* Siempre poner la última la principal para evitar problemas */}
          <Route path="/contacto" exact>
            <Redirect to="/garaje/contacto"></Redirect>
            {/* <Contacto correo={correo} telefono={telefono}></Contacto> */}
          </Route>
          <Route path="/garaje/contacto" exact>
            <Contacto correo={correo} telefono={telefono}></Contacto>
          </Route>
          {/* Pagina principal */}
          <Route path="/" exact>
            <Principal></Principal>
            <Redirect to="/garaje"></Redirect>
          </Route>
          <Route path="/garaje" exact>
            <Principal></Principal>
          </Route>
          <Route path="*" >
            <Principal></Principal>
          </Route>
        </Switch>
        
        <Pie correo={correo} telefono={telefono}/>
      </Router>
    </div>
  );
}

export default App;
