import React from "react";
import "../assets/css/contacto.css";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faMapMarkerAlt, faPhone} from '@fortawesome/free-solid-svg-icons'


import MetaTags from 'react-meta-tags';

class Contacto extends React.Component {
  render() {
    return (
      <div className="contacto">
          <MetaTags>
                <title>Contactanos para cualquier duda o reservas de citas</title>
                <meta name="description" content="En Garajes Guzman estamos a tu disposición contacta con nosotros sin ningún compromiso, reserva tu cita, solicita información" />
                {/* <meta property="og:title" content="" />
                <meta property="og:image" content="path/to/image.jpg" /> */}
            </MetaTags>
            <section className="info-inicio">
                <h1>El lavado de coche en León</h1>
                <p>Te estamos esperando, contacta con nosotros para cualquiera de los siguientes temas, 
                    te responderemos en el menor tiempo posible</p>
                <div>
                    <ul className="info">
                        <li>Solicitarnos más información</li>
                        <li>Te has quedado con dudas</li>
                        <li>Quieres <b>reservar tu cita</b></li>    
                    </ul>  
                </div>
            </section>
            
            <section className="info-contacto">
                <h1>Nuestros datos de contacto</h1>
                <div>
                    <p>
                        <FontAwesomeIcon className="icono" aria-hidden="true" icon={faPhone} />
                        Teléfono: {this.props.telefono} 
                    </p>
                    <p>
                        <FontAwesomeIcon className="icono" aria-hidden="true" icon={faEnvelope} />
                        Correo Electrónico: {this.props.correo}
                    </p>
                    <p>
                        <FontAwesomeIcon className="icono" aria-hidden="true" icon={faMapMarkerAlt} />
                        Calle Roa de la Vega, 32, 24001 León
                    </p>
                </div>
            </section>
      </div>
    );
  }
}

export default Contacto;