import React from "react";
import logo from "./../assets/imagenes/logo.png"
import "./../assets/css/menu.css";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars} from '@fortawesome/free-solid-svg-icons'
import { NavLink} from "react-router-dom"

class Principal extends React.Component {
    constructor() {
        super()
        this.state = {
            'active': false,
        };
        this.activarMenu = this.activarMenu.bind(this);
        this.desactivar = this.desactivar.bind(this)
    }

    activarMenu(){
        this.setState({
            'active': !this.state.active
        })
    }

    desactivar(){
        this.setState({
            'active': false,
        })
        window.scrollTo(0,0)
    }

    render() {
        return (
            <header>
                <img className="logo" src={logo} alt="Logo de Garajes Guzman" />
                
                <FontAwesomeIcon onClick={this.activarMenu} className="iconos desplegable"  icon={faBars} />

                <div className={this.state.active ? 'barra-menu-activa' : 'barra-menu'} id="menu">
                    <NavLink className="enlace" onClick={this.desactivar} to={"/"}>Inicio</NavLink>
                    {/* <NavLink className="enlace" onClick={this.desactivar} to={"/servicios"}>Servicios</NavLink> */}
                    <NavLink className="enlace" onClick={this.desactivar} to={"/contacto"}>Contacto</NavLink>
                </div>
            </header>
        );
    }
}

export default Principal;